import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { answers6 } from "../../../../atoms/answers6";
import { useSetRecoilState } from "recoil";

const StepText6 = () => {
  // 빈 입력값 방지를 위함
  const [answer1, setAnswer1] = useState("");
  const [answer2, setAnswer2] = useState("");
  const [answer3, setAnswer3] = useState("");

  const setAnswers = useSetRecoilState(answers6);

  useEffect(() => {
    setAnswers(`${answer1}, ${answer2}, ${answer3}`);
  }, [answer1, answer2, answer3, setAnswers]);

  return (
    <>
      <Text>
        [길이] 도구를 누른 후,
        <br />
        수직이등분선의 교점 B_1에서
        <br />
        각 꼭짓점 A, B, C까지의 거리를
        <br />
        측정해봅시다.
      </Text>
      <div>
        <Text style={{ marginTop: "20px", paddingLeft: "20px" }}>
          선분 A B_1의 길이 :{" "}
          <input
            value={answer1}
            onChange={(e) => setAnswer1(e.target.value)}
            style={{ width: "164px", marginLeft: "8px" }}
          />
        </Text>
        <Text style={{ margin: "10px 0", paddingLeft: "20px" }}>
          선분 B B_1의 길이 :{" "}
          <input
            value={answer2}
            onChange={(e) => setAnswer2(e.target.value)}
            style={{ width: "164px", marginLeft: "8px" }}
          />
        </Text>
        <Text style={{ paddingLeft: "20px" }}>
          선분 C B_1의 길이 :{" "}
          <input
            value={answer3}
            onChange={(e) => setAnswer3(e.target.value)}
            style={{ width: "164px", marginLeft: "8px" }}
          />
        </Text>
      </div>
    </>
  );
};

const Text = styled.div`
  font-family: NanumSquareR;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;
  line-height: 1.6;
`;

export default StepText6;
