import React from "react";
import { useRecoilValue } from "recoil";
import { stepState } from "../../../atoms/step";
import styled from "styled-components";
import step5_1 from "../../../img/slide3_step5_1.svg";
import step5_2 from "../../../img/slide3_step5_2.svg";
import StepText6 from "./shortanswerQ/StepText1-6";
import StepText8 from "./shortanswerQ/StepText1-8";

const StepText = () => {
  const step = useRecoilValue(stepState);

  return (
    <>
      {/** 1번 문제 */}
      {step === 1 && (
        <Text>
          [다각형] 도구를 누른 후,
          <br />빈 창을 자유롭게 세 번 클릭하여
          <br />
          꼭짓점을 3개 만든 후에
          <br />
          다시 처음 꼭짓점을 클릭하여
          <br />
          삼각형 1개를 그려봅시다.
        </Text>
      )}
      {step === 2 && (
        <Text>
          [수직이등분선] 도구를 누른 후,
          <br />
          두 점 A, B를 클릭하여 선분 AB의
          <br />
          수직이등분선을 작도하고
          <br />
          두 점 B, C를 클릭하여 선분 BC의
          <br />
          수직이등분선을 작도해봅시다.
        </Text>
      )}
      {step === 3 && (
        <Text>
          [교점] 도구를 누른 후,
          <br />
          두 수직이등분선을 클릭하여
          <br />
          두 수직이등분선의 교점 B_1을
          <br />
          표시합니다.
        </Text>
      )}
      {step === 4 && (
        <Text>
          [수직이등분선] 도구를 누른 후,
          <br />
          두 점 A, C를 클릭하여 선분 AC의
          <br />
          수직이등분선을 작도합니다.
          <br />
          선분 AC의 수직이등분선은
          <br />
          교점 B_1을 지나나요?
        </Text>
      )}
      {step === 5 && (
        <>
          <Text>
            왼쪽 수식창
            <img
              src={step5_1}
              alt="step5_1"
              style={{ margin: "0 4px 0 8px", height: "24px" }}
            />
            에서
          </Text>
          <Text>
            수직이등분선의 버튼
            <img
              src={step5_2}
              alt="step5_2"
              style={{ margin: "0 4px 0 8px", height: "24px" }}
            />{" "}
            을 눌러서
          </Text>

          <Text>
            세 수직이등분선을 숨깁니다.
            <br />
            [선분] 도구를 누른 후,
            <br />
            두 점 A와 B_1, 두 점 B와 B_1,
            <br />
            두 점 C와 B_1을 각각 클릭하여
            <br />
            수직이등분선의 교점과 각 꼭짓점을
            <br />
            잇는 세 선분을 그립니다.
          </Text>
        </>
      )}
      {step === 6 && <StepText6 />}
      {step === 7 && (
        <Text>
          [원 : 중심과 한 점] 도구를
          <br />
          누른 후, 두 점 B_1과 A를 차례대로
          <br />
          클릭하여 교점 B_1을 중심으로 하고
          <br />
          점 A를 지나는 원을 그려봅시다.
          <br />
          이 원은 나머지 두 꼭짓점 B, C를
          <br />
          지나나요?
        </Text>
      )}
      {step === 8 && <StepText8 />}
      {step === 9 && (
        <Text>
          모든 삼각형에서
          <br />
          세 번의 수직이등분선은 반드시
          <br />한 점에서 만날까요?
          <br />
          이를 어떻게 확인할 수 있을까요?
        </Text>
      )}
    </>
  );
};

const Text = styled.div`
  font-family: NanumSquareR;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;

  line-height: 1.6;
`;

export default StepText;
