import React from "react";
import { useRecoilValue } from "recoil";
import { stepState } from "../../../atoms/step";
import styled from "styled-components";
import { useParams } from "react-router-dom";

const Tip = () => {
  const { id } = useParams();
  const step = useRecoilValue(stepState);
  return (
    <>
      {id === "1" && step === 1 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
            알지오매스 TIP
          </span>
          <TipContent>
            도형을 잘못 그렸다면
            <br />
            좌측상단 화살표를 누르면
            <br />
            되돌리기 됩니다.
          </TipContent>
        </TipWrapper>
      )}
      {id === "1" && step === 2 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
            알지오매스 TIP
          </span>
          <TipContent>
            [수직이등분선] 도구는
            <br />
            [선분] 도구 밑에 있습니다.
          </TipContent>
        </TipWrapper>
      )}
      {id === "1" && step === 3 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
            알지오매스 TIP
          </span>
          <TipContent>
            기본 선택도구로 돌아가고싶다면
            <br />
            기보드의 ESC를 누르거나
            <br />
            [선택]도구를 클릭합니다.
          </TipContent>
        </TipWrapper>
      )}
      {id === "1" && step === 5 && (
        <TipWrapper>
          <span
            style={{
              width: "fit-content",
              backgroundColor: "#FFE8A0",
              padding: "2px 8px",
            }}
          >
            알지오매스 TIP
          </span>
          <TipContent>
            [선분] 도구는
            <br />
            [수직이등분선] 도구 위에 있습니다.
          </TipContent>
        </TipWrapper>
      )}
    </>
  );
};

const TipWrapper = styled.div`
  width: 100%;
  margin-top: 64px;
  padding: 0 24px;
  font-family: NanumSquareR;
  font-weight: 400;
  color: #4d4d4d;
  font-size: 16px;
`;

const TipContent = styled.div`
  padding-left: 8px;
  margin-top: 16px;
  font-size: 15px;
  line-height: 1.4;
`;

export default Tip;
