import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { answers8 } from "../../../../atoms/answers8";
import { useSetRecoilState } from "recoil";

const StepText8 = () => {
  // 각각의 입력값이 비어있지 않음을 체크하기 위함
  const [answer1, setAnswer1] = useState("");
  const [answer2, setAnswer2] = useState("");
  const [answer3, setAnswer3] = useState("");

  const setAnswers = useSetRecoilState(answers8);

  useEffect(() => {
    setAnswers(`${answer1}, ${answer2}, ${answer3}`);
  }, [answer1, answer2, answer3, setAnswers]);

  return (
    <>
      <Text style={{ flexDirection: "column" }}>
        <span>삼각형 ABC의 모든 꼭짓점이</span>
        <span>원 O의 위에 있을 때, 이 원 O는</span>
        <span style={{ marginBottom: "16px" }}>
          삼각형 ABC에{" "}
          <input
            value={answer1}
            onChange={(e) => setAnswer1(e.target.value)}
            style={{
              width: "64px",
              height: "24px",
              marginLeft: "2px",
              marginRight: "2px",
            }}
          />
          한다고 한다.
        </span>
        <span>
          이때 원 O를 삼각형 ABC의{" "}
          <input
            value={answer2}
            onChange={(e) => setAnswer2(e.target.value)}
            style={{
              width: "64px",
              height: "24px",
              marginLeft: "2px",
              marginRight: "2px",
            }}
          />
        </span>
        <span>이라 하고, 외접원의 중심 O를</span>
        <span>
          삼각형 ABC의{" "}
          <input
            value={answer3}
            onChange={(e) => setAnswer3(e.target.value)}
            style={{
              width: "64px",
              height: "24px",
              marginLeft: "2px",
              marginRight: "2px",
            }}
          />
          이라 한다.{" "}
        </span>
      </Text>
    </>
  );
};

const Text = styled.div`
  font-family: NanumSquareR;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;
  line-height: 1.6;
`;

export default StepText8;
